
@import url("https://fonts.googleapis.com/css?family=Lato|Noto+Serif");
.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3em;
}
.page .page-body {
    width: 60%;
    text-align: left;
    margin-bottom: 5em;
}
.page .page-body ul {
      list-style: none;
}
.page .page-body ul li {
        margin: 0em;
}
.page .page-body a {
      text-decoration: none;
}
.page .page-body a:hover {
        text-decoration: underline;
        color: #000;
}
.content h2 {
  font-family: "Lato", sans-serif;
  font-weight: 700;
  font-size: 2.5rem;
  color: #3e70c1;
  margin: .4em 0em .1em 0em;
}
.content h3 {
  font-family: "Lato", sans-serif;
  font-weight: 600;
  font-size: 1.5rem;
  color: #000;
  margin: .2em 0em;
}
.content h4 {
  font-family: "Lato", sans-serif;
  font-weight: 600;
  font-size: 1.5rem;
  color: #999;
  margin: 0em 0em .6em 0em;
}
.content h5 {
  font-family: "Lato", sans-serif;
  font-weight: 600;
  font-size: 1.2rem;
  margin: 1.2em 0em .6em 0em;
}
.content p {
  white-space: pre-wrap;
  font-family: "Noto Serif", serif;
  font-size: 1.15rem;
  margin: .2em 0em;
}
.header {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
}
.header .headshot {
    display: inline-block;
    height: 200px;
}
.header .info {
    margin-left: 2em;
}
.header .info h3 {
      margin: .2em 0em;
}
.header .info p {
      margin: .1em 0em;
}
.blogpost ul {
  margin: 0em;
  padding: 0em;
  list-style: none;
}



